.main {
    position: relative;
    .container {
        position: relative;
        width: 100%;
        height: 100%;
        margin-bottom: 128px;

        .logo-content {
            display: flex;
            justify-content: center;
            align-items: center;
            .logo {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                width: 120px;
                height: 320px;
                .logo-img {
                    width: 80px;
                    height: 80px;
                    margin-bottom: 14px;
                }
        
                .text-img {
                    width: 100%;
                }
            }
        }

        .brand-item {
            position: relative;
            width: 100%;
            display: flex;

            &:nth-child(2n) {
                flex-direction: row-reverse;
                .vertical-line {
                    left: 0;
                    @include mobile {
                        left: -1.2px;
                    }
                }
            }

            @include tablet {
                min-height: 200px;
                height: 100%;
            }

            .horizontal-line {
                position: absolute;
                top: 0;
                left: -3%;
                z-index: 10;
                width: 106%;
                height: 2.4px;
                opacity: 0.8;
            }

            .copy-content {
                display: flex;
                align-items: center;
                position: relative;
                width: 100%;
                height: 200px;
                max-height: 200px;
                padding: 24px 72px;
                transition: 0.3s ease;
                overflow: hidden;

                &:hover {
                    max-height: 300px;
                    .cover {
                        opacity: 0;
                    }
                    @include tablet {
                        height: 100%;
                    }
                    @include mobile {
                        height: 100%;
                    }
                }

                @include tablet {
                    align-items: start;
                    padding: 24px 48px;
                }

                @include mobile {
                    width: 50%;
                    align-items: start;
                    padding: 16px 12px;
                }

                .cover {
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    height: 30%;
                    background: linear-gradient(360deg, #FFF8F3 30.9%, rgba(255, 255, 255, 0) 82.54%);
                    opacity: 1;
                    transition: 0.3s ease;
                }
            }

            .logo-content {
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 200px;
                min-width: 200px;
                min-height: 200px;
                cursor: pointer;
                transition: 0.3s ease;

                &:hover {
                    opacity: 0.7;
                    background: $white-color;
                }

                @include mobile {
                    width: 50%;
                    min-width: 50%;
                }

                img {
                    width: 100%;
                    height: 100%;
                }

                .vertical-line {
                    position: absolute;
                    right: 0;
                    width: 2px;
                    height: 100%;
                    @include mobile {
                        right: -1.2px;
                    }
                }

                .br-logo {
                    width: 80px;
                    height: 80px;
                    @include mobile {
                        width: 64px;
                        height: 64px;
                    }
                }

                .forrest-lab-logo {
                    width: 112px;
                    height: 92px;
                    margin-left: 40px;
                    @include mobile {
                        width: 80px;
                        height: 64px;
                        margin-left: 20px;
                    }
                }

                .valeaan-logo {
                    width: 152px;
                    height: 72px;
                    @include mobile {
                        width: 104px;
                        height: 50px;
                    }
                }

                .bergandridge-logo {
                    width: 96px;
                    height: 42px;
                    @include mobile {
                        width: 72px;
                        height: 32px;
                    }
                }

                .bjonnd-logo {
                    width: 112px;
                    height: 56px;

                    @include mobile {
                        width: 96px;
                        height: 48px;
                    }
                }
            }
        }

        .container-vertical-line {
            position: absolute;
            top: 0;
            width: auto;
            height: 100%;
            object-fit: cover;
            opacity: 0.4;
        }

        .container-vertical-line-left{
            left: 0;
            z-index: 10;

            @include tablet {
                left:24px;
            }

            @include mobile {
                left: 16px;
            }
        }

        .container-vertical-line-right{
            right: 0;
            z-index: 10;
            @include tablet {
                right:24px;
            }
            @include mobile {
                right: 16px;
            }
        }

        .horizontal-line-bottom {
            position: absolute;
            bottom: 0;
            left: -3%;
            width: 106%;
            height: 2.4px;

            @include tablet {
                width: 100%;
                left: 0;
            }

            @include mobile {
                width: 100%;
                left: 0;
            }
        }
    }
}