#footer {
    height: 160px;
    background: #fff;
    .container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
        .contact-content {
            margin-bottom: 12px;
        }

        .copyright-content {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            .copy-body {
                display: block;
                color: $secondary-color;
            }

            .copy-instagram {
                margin-right: 18px;
                color: $primary-color;
                @include mobile {
                    margin-right: 0;
                    margin-bottom: 12px;
                }
            }

            @include mobile {
                flex-direction: column;
            }
        }
    }
}