@import "components/scss/config/asset.scss";
@import "components/scss/config/reset.scss";

@import "components/scss/footer.scss";

@import "components/scss/main.scss";

* {
    &::selection {
        background: #c4c4c4;
    }

    &::-moz-selection {
        background: #c4c4c4;
    }
}

body {
    background: #FFF8F3;
}

.section {
    display: flex;
    justify-content: center;

    @include tablet {
    }

    @include mobile {
    }
}

.container  {
    max-width: 976px;
    width: 100%;
    margin: 0 auto;
    @include tablet {
        max-width: 648px;
        padding: 0 24px;
    }
    @include mobile {
        padding: 0 24px;
    }
}

.copy-heading {
    @include font_heading;
    margin-bottom: 24px;
    color: $primary-color;
    
    @include mobile {
        margin-bottom: 18px;
    }
}

.copy-body {
    @include font_regular;
    color: $primary-color;
}

.copy-sub-heading {
    @include font_sub_heading;
    color: $primary-color;
}

.copy-small {
    @include font_small;
    color: $primary-color;
}

img {
    display: block;
    width: 100%;
}